let $lastLi = $(".ulList>.lastLi");
let hash = JSON.parse(localStorage.getItem("x"));
let hashMap = hash || [
  { logo: "B", url: "baidu.com" },
  { logo: "Y", url: "yaochengjian.com" }
];

/* æ¸²æŸ“æ•°æ® */
function hashEach() {
  hashMap.forEach(function(item) {
    $(`          
  <li>
  <a href="${"https://" + item.url}">
    <div>${item.logo}</div>
    <p>${item.url}</p>
  </a>
  <span class="close">
    <svg class="icon" aria-hidden="true">
      <use xlink:href="#icon-close"></use>
    </svg>
  </span>
</li>
    `).insertBefore($lastLi);
  });
  remove();
}
hashEach();

/* ç‚¹å‡»å¢åŠ ä¸€ä¸ªæ ‡ç­¾ */
$(".lastLiTwo").on("click", function() {
  let url1 = window.prompt("è¯·è¾“å…¥ä½ æ–°å¢çš„ç½‘å€");
  let newUrl2 = null;
  let bool = /^(?:http(s)?:\/\/)?[\w.-]+(?:\.[\w\.-]+)+[\w\-\._~:/?#[\]@!\$&'\*\+,;=.]+$/.test(
    url1
  );
  let initials;
  /* åŒ¹é…æ˜¯æœ‰æ•ˆç½‘å€çš„è¯å°±ä¼šè¿›è¡Œç­›é€‰åé¢éƒ¨åˆ† */
  if (bool) {
    newUrl2 = url1.replace(/(https:\/\/)|(http:\/\/)|(www.)/g, "");
    newUrl2 = newUrl2.replace(/(\/.*)/g, "");
    initials = newUrl2[0].toLocaleUpperCase();
  } else {
    alert("æ— æ•ˆç½‘å€");
    return;
  }

  $(".ulList")
    .find("li:not(.lastLi)")
    .remove();
  hashMap.push({ logo: initials, url: newUrl2 });
  hashEach();
});

/* ç½‘é¡µç¦»å¼€å­˜æ•°æ® */
window.onbeforeunload = function() {
  let str = JSON.stringify(hashMap);
  localStorage.setItem("x", str);
  console.log("ç½‘é¡µè¦ç¦»å¼€äº†");
};

/* åˆ é™¤ */
function remove() {
  let closes = document.querySelectorAll(".close");
  console.log(closes);
  for (let i = 0; i < closes.length; i++) {
    closes[i].onclick = function() {
      hashMap.splice(i, 1);
      $(".ulList")
        .find("li:not(.lastLi)")
        .remove();
      hashEach();
    };
  }
}
remove();
